<template>
<div>
<!-- <v-navigation-drawer
      persistent
      :mini-variant="miniVariant"
      :clipped="clipped"
      v-model="drawer"
      enable-resize-watcher
      fixed
      app
    >
      <v-list>
        <v-list-tile
          value="true"
          v-for="(item, i) in items"
          :key="i"
        >
          <v-list-tile-action>
            <v-icon v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer> -->
    <v-toolbar
      app
      :clipped-left="clipped"
    >
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
     <!--  <v-btn icon @click.stop="miniVariant = !miniVariant">
        <v-icon v-html="miniVariant ? 'chevron_right' : 'chevron_left'"></v-icon>
      </v-btn>
      <v-btn icon @click.stop="clipped = !clipped">
        <v-icon>web</v-icon>
      </v-btn>
      <v-btn icon @click.stop="fixed = !fixed">
        <v-icon>remove</v-icon>
      </v-btn> -->
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
     <v-avatar>
      <img :src="me.avatar" alt="username">
    </v-avatar> 

          
      <!-- <v-btn color="primary" dark>{{me.name}}
        <v-icon dark right>person</v-icon>
      </v-btn> -->
<!--       <v-btn icon @click.stop="login">
        <v-icon>person</v-icon>
      </v-btn> -->
    </v-toolbar>
</div>
</template>

<script>
export default {
  data () {
    return {
      clipped: false,
      drawer: true,
      fixed: false,
 /*      items: [{
        icon: '',
        title: ''
      }], */
      items: [],
      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: '議題分析表',
      me: {
        'name':'',
        'avatar': ''
      },
      message: 
        {
          avatar: 'https://avatars0.githubusercontent.com/u/9064066?v=4&s=460',
          name: 'John Leider',
          title: 'Welcome to Vuetify.js!',
          excerpt: 'Thank you for joining our community...'
        },
      
    }
  },
  methods: {
    login: function () {
      Trello.authorize({name: '議題分析表' ,expiration:'never',scope: { read: true, write: true },})
    },
    menu: function () {
      console.log(this.items)
    },
    getsuccessed: function(res) {
      console.log(res)
    }
  },
  created: function() {
    let that = this
    let a = Trello.organizations.get('ibm249/boards',{'filter':'all'}, function(res) {
      this.items = []
      res.map(b => {
        let item = {};
        item.icon = 'bubble_chart'
        item.title = b.name
        this.items.push(item)
      })
    })
    Trello.members.get('me', function (res) {
      that.me.name = res.username,
      that.me.avatar = res.avatarUrl + '/50.png'
      //console.log(that.me)
    },this.login())
  }
}
</script>

<style>

</style>



